import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { gradedExercises } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    name: '',
    nameEn: '',
    fromScores: '',
    toScores: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formGraded' })
class FormGradedExercises extends Component {
  state = {
    gradedId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ gradedId: match.params.id });
      this.getGradedById(match.params.id);
    }
  };

  getGradedById = name => {
    const gradedMap = gradedExercises.filter(graded => graded.name === name)[0];
    const initialValue = handleInitialValue(gradedMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { gradedId } = this.state;
    return (
      <Card title={gradedId ? 'Cập nhật xếp loại rèn luyện' : 'Thêm xếp loại rèn luyện'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Xếp loại rèn luyện" {...formItemLayout}>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('nameEn', {
                initialValue: initialValue.nameEn,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm" hasFeedback>
              {getFieldDecorator('fromScores', {
                initialValue: initialValue.fromScores,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm" hasFeedback>
              {getFieldDecorator('toScores', {
                initialValue: initialValue.toScores,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormGradedExercises {...props} />
  </Page>
));



